.home {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    gap: 20px;
    /* background-color: green; */
}

.mask {
    position: absolute;
    top: 20%;
    left: 7%;
    width: 50%;
}

.img {
    position: absolute;
    width: 50%;
    height: 100%;
   top: 1%;
    left: 50%;
}

.img img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}