.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 78px;
    z-index: 10;
    background: transparent;
    padding-left: 50px;
    letter-spacing: 1.5px;
    color: rgb(233, 220, 220);
    background-color: #013045;
}

.header-bg {
    background-color: black;
    transition: 0.6s;
   

}

.nav-menu {
    display: flex;

}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 600;
}

.hamburger {
    display: none;
}

@media screen and (max-width:1040px) {

    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        position: absolute;
        background-color: black;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.4s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger {
        display: initial;
    }
}