.head{
    position: relative;
    width: 100%;
    height: 80vh;
   

}
.main{
    position: absolute;
   
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
    width: 80%;
    margin: auto;
}
@media screen and (max-width:900px) {
    .head{
        position: relative;
        width: 100%;
        height: 50vh;
       
    
    }
}