.skill-main {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: #013045;


    color: white;
}

.skill-main .skill-child {
    position: absolute;
  
    transform: translate(-50%, -50%);
    top: 20%;
    left: 50%;
    text-align: center;
    width: 80%;
    margin: auto;

}

.techincal-skills {
    position: absolute;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: auto;
    text-align: center;
    gap: 30px;
}

@media screen and (max-width:800px) {

    .techincal-skills {
        position: absolute;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        margin: auto;
        text-align: center;
        gap: 30px;
    }
    .skill-main{
        height: 120vh;
    }
}

.front-end {
    display: grid;
    margin-top: 35px;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    justify-items: center;
    
}
.border{
   
    border-radius: 20px;
    padding: 10px;
    background-color: #044361;
}