.project {
    position: relative;
    width: 100%;
    height:230vh;
    background-color: #013045;
   
}

.content-project {
    position: absolute;
   
    width: 100%;
    height: auto;
    background-color: #013045;
    top: 110px;
    
    color: white;
  
}
.flexcontent{
    width: 95%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 20px;
    grid-template-rows: auto;
    padding: 10px;
    margin: auto;
}
@media screen and (max-width:700px){
    .flexcontent{
        width: 95%;
        height: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 30px;
        grid-template-rows: auto;
        padding: 10px;
        margin: auto;
    }
    .project {
        position: relative;
        width: 100%;
        height:350vh;
        background-color: #013045;
       
    }
}
@media screen and (max-width:550px){
    .project {
        position: relative;
        width: 100%;
        height:300vh;
        background-color: #013045;
       
    }
}
.main-content {
  
    /* width: 45%; */
    height: auto;
   
    background-color: rgb(58, 56, 56);
 
    border-radius: 10px;

    
}
.gitcode{
    border: none;
    background-color: yellow;
    color: black;

}
.gitcode:hover{
    border: none;
    background-color: rgb(17, 225, 69);
   
}
